@import url('https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap');
a{
    color:black !important ;
}
.brand-logo,h2{
    font-family: 'Grand Hotel', cursive;
}
.mycard{
    margin-top: 30px;
}
.auth-card{
    padding: 20px;
    text-align: center;
    max-width: 400px;
    margin: 10px auto;
}
.input-field input[type=text]:focus {
    border-bottom: 1px solid rgb(8, 93, 252) !important;
    box-shadow: 1px solid rgb(8, 93, 252) !important;
  }
  .input-field input[type=password]:focus {
    border-bottom: 1px solid rgb(8, 93, 252) !important;
    box-shadow: 1px solid rgb(8, 93, 252) !important;
  }
  .gallery{
    display: flex;
    flex-wrap: wrap;
    justify-content:space-around;
  }
  .item{
    width: 30%;
  }
  .home-card{
    max-width: 550px;
    height: max-content;
    margin: 26px auto;
  }

  #toast-container{
    top: 30px  !important;
    right: 20px !important;
    left: auto !important;
  }

  .update {
    margin-top: 5px;
    margin-left: 5px;
}